.statistics {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #e6ecf3;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.25);
}

.title {
    margin: 30px 0;
    padding: 0;
    font-size: 20px;
    text-transform: uppercase;
}

.statList {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
}